import { Transform, Type } from 'class-transformer';
import {
  IsIn,
  IsNotEmpty,
  IsNumber,
  IsOptional,
  IsString,
  MaxLength,
  Min,
} from 'class-validator';

/**
 * We accept lowercase ("physical"/"digital"/"service") and lowercase status ("active"/"inactive")
 * but normalize to UPPERCASE before validation using Transform.
 */
const TYPE_VALUES = ['PHYSICAL', 'DIGITAL', 'SERVICE'] as const;
type TypeUpper = typeof TYPE_VALUES[number];

const STATUS_VALUES = ['ACTIVE', 'INACTIVE'] as const;
type StatusUpper = typeof STATUS_VALUES[number];

export class CreateProductDto {
  @IsString()
  @IsNotEmpty()
  @MaxLength(255)
  title!: string;

  @IsString()
  @IsNotEmpty()
  @Transform(({ value }) => (value == null ? value : String(value).toUpperCase()))
  @IsIn(TYPE_VALUES as unknown as string[])
  type!: TypeUpper;

  @IsString()
  @IsNotEmpty()
  @Transform(({ value }) => (value == null ? value : String(value).toUpperCase()))
  @IsIn(STATUS_VALUES as unknown as string[])
  status!: StatusUpper;

  @Type(() => Number)
  @IsNumber({ maxDecimalPlaces: 2 })
  @Min(0)
  price!: number;

  @IsOptional()
  @IsString()
  @MaxLength(10_000)
  description?: string | null;

  /**
   * Optional SKU. If omitted, your service can generate one.
   * Accept any non-empty string up to 255 chars.
   */
  @IsOptional()
  @IsString()
  @MaxLength(255)
  @Transform(({ value }) => (value == null || value === '' ? undefined : String(value)))
  sku?: string;
}